#passageChooser {
  padding: 10px;
  padding-bottom: 50px;
}
.testament-tabs {
  margin: 10px 0px;
}
.testament-tabs button {
  margin: 0px 5px;
  outline: none;
  background-color: #3388cc;
  padding: 10px;
  border: none;
  border-radius: 4px;
  color: white;
  font-size: 18px;
}
.book {
  padding: 4px;
  float: left;
  border: solid 1px #3388cc;
  margin: 2px;
  margin-bottom: 8px;
  border-radius: 4px;
}
.book:hover {
  border-color: #ff7700;
}
.book-name {
  padding: 7px 5px 7px 5px;
  text-align: center;
  border-radius: 2px;
  font-size: 18px;
  cursor: pointer;
}
.selected-book {
  font-size: 18px;
  padding: 7px 5px 7px 5px;
  border-radius: 1px;
  letter-spacing: 2px;
}
.chapter {
  display: inline-block;
  width: 30px;
  text-align: center;
  padding: 15px 10px;
  margin: 2px;
  margin-bottom: 8px;
  border: solid 1px #3388cc;
  border-radius: 4px;
  cursor: pointer;
}
.chapter:hover {
  border-color: #ff7700;
}



